html, body {
  width: 100vw;
  height: 100vh;
}

img {
  max-width: 100%;
  height: auto;
  margin: 0;
}

body {
  perspective: 1000px;
  background: url("baba_s_frazoi.79fd958e.jpg") top / contain no-repeat;
  margin: 0;
  padding: 30px 0;
  font-family: Caveat, cursive;
  overflow: hidden;
}

.greet {
  background: url("we.8347828f.jpg") center / cover no-repeat;
  overflow: hidden;
}

.dot {
  width: 10px;
  height: 10px;
  background: url("snow.7388ce59.png") 0 0 / 100% 100%;
  position: absolute;
}

#petals {
  max-width: 100%;
  max-height: 100%;
}

.card {
  width: 90vw;
  z-index: 1;
  opacity: 0;
  background-color: #fff;
  padding: 15px 30px;
  animation: card 2s linear forwards;
  position: absolute;
  top: 0;
  left: 0;
  box-shadow: 0 0 20px rgba(0, 0, 0, .1);
}

.title {
  background-image: linear-gradient(110deg, #ff6945 0%, #8047ca 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  color: #ff6945;
  text-align: center;
  margin-bottom: 20px;
  font-size: 70px;
  font-weight: 700;
  line-height: 1;
}

.text {
  color: #333;
  margin-bottom: 20px;
  font-size: 32px;
  font-weight: 700;
  line-height: 1.2;
}

.author {
  background-image: linear-gradient(110deg, #ff6945 0%, #8047ca 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  opacity: 0;
  color: #121212;
  text-align: right;
  padding-right: 40px;
  font-size: 40px;
  transition: opacity 3s linear 1s;
}

@keyframes card {
  from {
    opacity: 0;
  }

  to {
    opacity: .6;
  }
}

.sun {
  width: 150px;
  height: 150px;
  animation: sun 5s linear forwards, spin 7s linear 3s infinite;
  display: none;
  position: absolute;
  top: -100px;
  left: 120px;
}

@keyframes sun {
  from {
    top: -100px;
  }

  60% {
    transform: rotate(0deg);
  }

  to {
    top: 10px;
    transform: rotate(360deg);
  }
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

.face {
  width: 80px;
  height: 87px;
  opacity: 0;
  animation: face 3s ease-in-out 8s forwards;
  display: none;
  position: absolute;
  top: 33%;
  left: 47%;
}

@keyframes face {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

.cat {
  width: 100px;
  height: 100px;
  opacity: 0;
  animation: heart 4s ease-in-out 5s forwards;
  display: none;
  position: absolute;
  top: 67%;
  right: -10%;
}

.dog {
  width: 100px;
  height: 200px;
  animation: dog 2s 21s forwards;
  position: absolute;
  top: 81%;
  right: -40%;
}

@keyframes dog {
  from {
    right: -40%;
  }

  to {
    right: -10%;
  }
}

.me {
  animation: me 5s linear 12s forwards;
  display: none;
  position: absolute;
  top: 120%;
  left: 31%;
}

@keyframes me {
  from {
    top: 120%;
  }

  to {
    top: 65%;
  }
}

.phrase {
  width: 200px;
  opacity: 0;
  background-color: rgba(0, 0, 0, 0);
  animation: heart 2s linear 22s forwards;
  display: none;
  position: absolute;
  top: 45%;
  left: 50%;
}

.heart {
  width: 60px;
  opacity: 0;
  animation: heart 2s linear 18s forwards;
  display: none;
  position: absolute;
  top: 54%;
  left: 70%;
}

@keyframes heart {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

.btn {
  cursor: pointer;
  position: absolute;
  top: 30%;
  left: 30%;
}

.is-hidden {
  display: block;
}

.link {
  cursor: pointer;
  font-size: 50px;
  text-decoration: none;
  position: absolute;
  bottom: 10%;
  left: 5%;
}

.cta {
  background-color: rgba(0, 0, 0, 0);
  border: none;
  margin: auto;
  padding: 19px 22px;
  transition: all .2s;
  position: relative;
  top: 42%;
  left: 28%;
}

.cta:before {
  content: "";
  width: 60px;
  height: 60px;
  background: #ffab9d;
  border-radius: 30px;
  transition: all .3s;
  display: block;
  position: absolute;
  top: 0;
  left: 0;
}

.cta:after {
  content: "";
  width: 60px;
  height: 60px;
  background: #ffab9d;
  border-radius: 30px;
  transition: all .3s;
  display: block;
  position: absolute;
  top: 0;
  right: 19px;
}

.cta span {
  letter-spacing: .25em;
  text-transform: uppercase;
  vertical-align: middle;
  z-index: 5;
  font-size: 16px;
  font-weight: 900;
  line-height: 18px;
  position: relative;
}

.cta svg {
  fill: none;
  stroke-linecap: round;
  stroke-linejoin: round;
  stroke: #111;
  stroke-width: 2px;
  z-index: 5;
  margin-left: 10px;
  transition: all .3s;
  position: relative;
  top: 0;
  transform: translateX(-5px);
}

.cta:hover.cta:before, .cta:hover.cta:after {
  width: 100%;
  background-color: rgba(0, 0, 0, 0);
}

.cta:hover.cta svg {
  display: none;
}

.cta:hover.cta span {
  color: rgba(0, 0, 0, 0);
}

